
import { Component, Vue, Ref, Prop } from 'vue-property-decorator';

@Component({
    components: {},
    mixins: [],
})
export default class DrawerDrag extends Vue {
    @Prop()
    protected readonly navigationWidth: string | number = 300;

    @Ref()
    drawer: any;

    private navigation: any = {
        shown: true,
        width: this.navigationWidth,
        borderSize: 4,
    };

    get direction(): string {
        return this.navigation.shown ? 'Closed' : 'Open';
    }

    mounted() {
        this.setBorderWidth();
        this.setEvents();
    }

    setBorderWidth(): void {
        const i = this.drawer.$el.querySelector('.v-navigation-drawer__border');
        i.style.width = this.navigation.borderSize + 'px';
        i.style.cursor = 'ew-resize';
    }

    setEvents(): void {
        const minSize = this.navigation.borderSize;
        const el = this.drawer.$el;
        const drawerBorder = el.querySelector('.v-navigation-drawer__border');
        const vm = this;
        const direction = el.classList.contains('v-navigation-drawer--right') ? 'right' : 'left';

        function resize(e) {
            document.body.style.cursor = 'ew-resize';
            let f = 'right' === direction ? document.body.scrollWidth - e.clientX : e.clientX;
            el.style.width = f + 'px';
        }

        drawerBorder.addEventListener(
            'mousedown',
            function (e) {
                if (e.offsetX < minSize) {
                    const mPos = e.x;
                    el.style.transition = 'initial';
                    document.addEventListener('mousemove', resize, false);
                }
            },
            false,
        );

        document.addEventListener(
            'mouseup',
            function () {
                el.style.transition = '';
                vm.navigation.width = el.style.width;
                document.body.style.cursor = '';
                document.removeEventListener('mousemove', resize, false);
            },
            false,
        );
    }
}
